const axios = require('axios');

const token = localStorage.getItem('token');
const username = localStorage.getItem('username');
const API_URL = "https://imite.herokuapp.com"
// const API_URL = "http://localhost:3001"

export async function likePost(postId){
    axios.post(API_URL + '/social/like/post', {username: username, token: token, postId: postId}).then(response => {
        return response.data;
    }).catch(error => {
        console.log(error);
        return error;
    });
}

export async function signIn(username, password){
    axios.post(API_URL + '/auth/signin', {username: username, password: password}).then(response => {
        localStorage.setItem('username', username);
        localStorage.setItem('token', response.data);
        window.location.reload();
        return response.data;
    }).catch(error => {
        console.log(error);
        return error;
    });
}

export async function signUp(email, username, password){
    axios.post(API_URL + '/auth/signup', {email: email, username: username, password: password}).then(response => {
        return response.data;
    }).catch(error => {
        console.log(error);
        return error;
    });
}

export async function getInfo(_username){
    const result = await axios.post(API_URL + '/social/user/info', {username: username, token: token, user: _username});
    return result.data;
}

export async function followUser(_username){
    const result = await axios.post(API_URL + '/social/follow', {username: username, token: token, follow: _username});
    return result.data;
}

export async function post(file, caption) {
    const formData = new FormData();
    formData.append('image', file);
    formData.append('username', username);
    formData.append('token', token);
    formData.append('caption', caption);
    const result = await axios.post(API_URL + '/social/post', formData);
    return result.data;
}

export async function pfp(file){
    const formData = new FormData();
    formData.append('file', file);
    formData.append('username', username);
    formData.append('token', token);
    const result = await axios.post(API_URL + '/upload/profile/picture', formData);
    return result.data;
}

export async function bio(bio){
    const result = await axios.post(API_URL + '/social/bio', {username: username, token: token, bio: bio});
    return result.data;
}

export async function feed(){
    const result = await axios.post(API_URL + '/social/home/feed', {username: username, token: token});
    return result.data;
}

export async function search(query){
    const result = await axios.post(API_URL + '/social/search', {username: username, token: token, search: query});
    return result.data;
}

export function pfpUrl(username){
    return API_URL + '/social/pfp/' + username;
}