import './SignInUp.css';
import { useState, useEffect} from 'react';
import {signIn, signUp} from './Api';

import { Text, Button, Input, Modal, Divider, Loading } from '@nextui-org/react';

function SignIn(props) {
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [signUp, setSignUp] = useState(false);
    const [loading, setLoading] = useState(false);

    function _signIn(username, password){
        setLoading(true);
        signIn(username, password).then((response) => {
            setLoading(false);
            console.log('finished: ' + response)
            return response;
        });
    }
    
    return (
        <Modal
        open={props.open}
        onClose={() => {
            props.onClose();
        }}
        blur
        >
        <Modal.Header>
            <Text h1>Sign In</Text>
        </Modal.Header>
        <Modal.Body>
            <Input
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            />
            <Input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            />
        </Modal.Body>
        <Modal.Footer>
            <Button
            auto
            color="primary"
            onClick={() => {
                props.onClose();
                _signIn(username, password).then((response) => {
                    setLoading(false);
                    //window.location.reload();
                })
            }}
            loading={loading}
            >
            {loading ? <Loading color="currentColor" size="sm" /> : 'Sign In'}
            </Button>
        </Modal.Footer>
        </Modal>
    );
}

function SignUp(props) {
    const [email, setEmail] = useState('');
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [loading, setLoading] = useState(false);

    function _signUp(email, username, password){
        setLoading(true);
        signUp(email, username, password).then((response) => {
            setLoading(false);
            console.log('finished: ' + response)
            return response;
        });
    }
    
    return (
        <Modal
        open={props.open}
        onClose={() => {
            props.onClose();
        }}
        blur
        >
        <Modal.Header>
            <Text h1>Sign Up</Text>
        </Modal.Header>
        <Modal.Body>
        <Input
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            type="email"
            />
            <Input
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            />
            <Input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            />
        </Modal.Body>
        <Modal.Footer>
            <Button
            auto
            color="primary"
            onClick={() => {
                props.onClose();
                _signUp(email, username, password).then((response) => {
                    setLoading(false);
                    //window.location.reload();
                })
            }}
            loading={loading}
            >
            {loading ? <Loading color="currentColor" size="sm" /> : 'Sign In'}
            </Button>
        </Modal.Footer>
        </Modal>
    );
}

export default function SignInUp(){
    const [signInOpen, setSignInOpen] = useState(false);
    const [signUpOpen, setSignUpOpen] = useState(false);

    return (
        <div className="siu">
            <Text h1>Welcome to Social Media</Text>
            <SignIn onClose={() => setSignInOpen(false)} open={signInOpen} onSignIn={(username, password) => {
                console.log(username, password);
            }}/>

            <SignUp onClose={() => setSignUpOpen(false)} open={signUpOpen} onSignUp={(username, password) => {
                console.log(username, password);
            }}/>
            <Button onClick={() => setSignUpOpen(true)}>Sign Up!</Button>
            <br></br>
            <Text>or</Text>
            <br></br>
            <Button onClick={() => setSignInOpen(true)} color="primary">Sign In</Button>
        </div>
    );
}