import {useState, useEffect} from 'react';
import { Text, Card, Row, Button} from '@nextui-org/react';
import {feed, likePost} from './Api';
import {HeartIcon} from './resources/HeartIcon'
import {Link} from 'react-router-dom'
import PostView from './components/PostView';
import './Home.css'

function HomePost(props){
    const [liked, setLiked] = useState(props.likedByYou);
    const [likes, setLikes] = useState(props.likes);
    return (
        <div className="post">
        <Card isHoverable onPress={() => {
            props.onPress();
        }} isPressable>
            <Card.Body css={{ p: 0 }}>
                <Card.Image
                    src={props.img}
                    className="pressable"
                    objectFit="cover"
                    width="100%"
                    height="200px"
                    showSkeleton
                    alt={props.username}
                />
            </Card.Body>
            <Card.Footer css={{ justifyItems: "flex-start" }} className="noHoverCursor">
                <Row wrap="wrap" align="center">
                    <Text css={{ width: "calc(100% - 100px)", textAlign: "left" }}><Link className="noLink" to={props.username}><span className="noLink" id="username">@{props.username}</span></Link> {props.caption}</Text>
                    <div className="likeButton" css={{ width: "100px",}}><Button
                    color="error"
                    bordered={!liked}
                    auto
                    onPress={() => {
                        likePost(props.id);
                        setLiked(!liked);
                        if(liked) setLikes(likes - 1);
                        else setLikes(likes + 1);
                        props.toggleLikedByYou();
                    }}
                    css={{right: "20px", position: "absolute", bottom: "-10px", width: "1%"}}
                    shadow={liked}
                    icon={<HeartIcon fill="currentColor" filled={liked} />}
                >{likes}</Button></div>
                </Row>
            </Card.Footer>
        </Card>
        </div>
    );
}

export default function Home(){
    //const username = localStorage.getItem('username');
    const [data, setData] = useState(undefined);
    const [postCaption, setPostCaption] = useState("");
    const [postImage, setPostImage] = useState("");
    const [postOpen, setPostOpen] = useState(false);
    const [postLikedByYou, setPostLikedByYou] = useState(false);
    const [postLikes, setPostLikes] = useState(0);
    const [postId, setPostId] = useState("");
    const [poster, setPoster] = useState("");


    useEffect(() => {
        feed().then(async (response) => {
            await setData(response);
            console.log(response);
            //console.log(response);
        });
    }, []);
    return (
        <div className="home">
            <PostView username={poster} open={postOpen} postId={postId} caption={postCaption} likedByYou={postLikedByYou} likes={postLikes} image={postImage} onClose={() => setPostOpen(false)} toggleLikedByYou={() => {
            let oldData = data;
            for(let i = 0; i < oldData.length; i++){
                const post = oldData[i];
                if(post.id === postId){
                oldData[i].likedByYou = !oldData[i].likedByYou;
                if(oldData[i].likedByYou) oldData[i].likes++;
                else oldData[i].likes--;
                setPostLikes(oldData[i].likes);
                setPostLikedByYou(oldData[i].likedByYou);
                setPostCaption("Loading...");
                setPostCaption(oldData[i].caption);
                }
            }
            setData(oldData);
        }}/>
            <div className="posts">
                {data === undefined ? null : data.map((post) => (
                    <HomePost
                        id={post.id}
                        username={post.poster}
                        caption={post.caption}
                        img={post.url}
                        likes={post.likes}
                        likedByYou={post.likedByYou}
                        toggleLikedByYou={() => {
                            post.likedByYou = !post.likedByYou;
                            if(post.likedByYou) post.likes++;
                            else post.likes--;
                        }}
                        onPress={() => {
                            setPostCaption(post.caption);
                            setPostImage(post.url);
                            setPostOpen(true);
                            setPostLikedByYou(post.likedByYou);
                            setPostLikes(post.likes);
                            setPostId(post.id);
                            setPoster(post.poster);
                        }}/>
                ))}
            </div>
        </div>
    )
}