import './Account.css';
// Import User object from nextui
import { Text, Avatar, Card, Row, Button} from '@nextui-org/react';
import { useEffect, useState } from 'react';
import {useParams} from 'react-router-dom'
import PostView from './components/PostView';
import {getInfo, followUser, pfpUrl} from './Api';
import {FollowIcon} from './resources/FollowIcon';


function PostPreview(props){
    return (
      <Card isPressable isHoverable onPress={props.onPress}>
        <Card.Body css={{ p: 0 }}>
          <Card.Image
            src={props.img}
            objectFit="cover"
            width="100%"
            height="200px"
            showSkeleton
            alt={props.username}
          />
        </Card.Body>
        <Card.Footer css={{ justifyItems: "flex-start" }}>
          <Row wrap="wrap" align="center">
            <Text><span id="username">@{props.username}</span> {props.caption}</Text>
          </Row>
        </Card.Footer>
      </Card>
    )
}

export function Account(props){
  let {username} = useParams();
  const [data, setData] = useState({});
  const [postData, setPostData] = useState({});
  const [userExists, setUserExists] = useState(true);

  const [postCaption, setPostCaption] = useState("");
  const [postImage, setPostImage] = useState("");
  const [postOpen, setPostOpen] = useState(false);
  const [postLikedByYou, setPostLikedByYou] = useState(false);
  const [postLikes, setPostLikes] = useState(0);
  const [postId, setPostId] = useState("");
  const [following, setFollowing] = useState(false);

  useEffect(() => {
    getInfo(username).then(async (response) => {
      await setPostData(response.posts);
      await setData(response);
      await setFollowing(response.followedByYou);
      //console.log(response);
      setUserExists(true);
    }).catch(error => {
      if(error != null && error.response != null && error.response.status === 400 ) setUserExists(false);
    });
  }, [username]);

  if(!userExists){
    return (
      <div>
      <Avatar className="center" css={{ size: "100px" }}/>
      <Text h1 className="center">User doesn't exist</Text>
      </div>
    )
  }
  if(data === undefined){

    return (
      <div>
      <Avatar className="center" css={{ size: "100px" }}/>
      <Text h1 className="center">Loading...</Text>
      </div>
    )
  }
  return(
      <div className="account">
        <PostView username={username} open={postOpen} postId={postId} caption={postCaption} likedByYou={postLikedByYou} likes={postLikes} image={postImage} onClose={() => setPostOpen(false)} toggleLikedByYou={() => {
          let oldData = postData;
          for(let i = 0; i < oldData.length; i++){
            const post = oldData[i];
            if(post.id === postId){
              oldData[i].likedByYou = !oldData[i].likedByYou;
              if(oldData[i].likedByYou) oldData[i].likes++;
              else oldData[i].likes--;
              setPostLikes(oldData[i].likes);
              setPostLikedByYou(oldData[i].likedByYou);
            }
          }
          setPostData(oldData);
          //console.log(oldData);
        }}/>
          <h1 className="center username">{username}</h1>
          <Avatar className="center" src={pfpUrl(username)} css={{ size: "100px" }} text={username.charAt(0).toUpperCase()}/>
          <Text h2 className="center" weight="normal">{data.bio}</Text>
          <Text h2 className="center" weight="light">{data.followers} Followers, Following {data.following}</Text>
          <Button className="center" onClick={() => {
            let newData = data;
            newData.followedByYou = !newData.followedByYou;
            if(newData.followedByYou) newData.followers++;
            else newData.followers--;

            if(username === localStorage.getItem('username')){
              if(newData.followedByYou) newData.following++;
              else newData.following--;
            }

            console.log(newData);
            setData(newData);
            setFollowing(newData.followedByYou);
            followUser(username);
            console.log(data);
          }} icon={<FollowIcon />} auto shadow={!following} bordered={following}></Button>
          <div className="posts">
          {postData.length > 0 ? postData.map((post) => (
              <div className="post"><PostPreview username={username} caption={post.caption} img={post.url} likes={post.likes} onPress={() => {
                setPostCaption(post.caption);
                setPostImage(post.url);
                setPostOpen(true);
                setPostLikes(post.likes);
                setPostId(post.id);
                setPostLikedByYou(post.likedByYou);
              }}/></div>
            )) : null}
          </div>
      </div>
  )
}