import './HeaderFooter.css';
import {Avatar, Button, Popover, Input, User} from '@nextui-org/react';
import {PlusIcon} from '../resources/PlusIcon'
import {EditIcon} from '../resources/EditIcon'
import { HomeIcon } from '../resources/HomeIcon';
import {Link} from 'react-router-dom'
import {useState} from 'react';
import UserEdit from './UserEdit';
import Post from './Post';
import {FollowIcon} from '../resources/FollowIcon';
import {SearchIcon} from '../resources/SearchIcon';
import { search, pfpUrl} from '../Api';

function signOut(){
  localStorage.clear();
  window.location.reload();
}

function UserPopoverContent(props){
  const username = localStorage.getItem('username');
  return (
    <div>
      <Link to={username} className="noLink"><Button css={{fontSize: "20px", textAlign: "right", color: "white"}} icon={<FollowIcon />} light>Profile</Button></Link>
      <Link to='/' className="noLink"><Button css={{fontSize: "20px", textAlign: "right"}} color="error" icon={<FollowIcon />} light onPress={() => signOut()}>Log out</Button></Link>
    </div>
  )
}

function SearchPopoverContent(props){
  const [data, setData] = useState();

  function _search(query){
    search(query).then(result => {
      setData(result);
    });
  }

  return (
    <div>
      <Input placeholder="Search" contentLeft={<SearchIcon size="40px" fill="black" />} css={{color: "#fff"}} onChange={(e) => {
        if(e.target.value && e.target.value.length > 0){
          _search(e.target.value)
        }
      }}/>
      <div>
        {data && data.map((item, index) => {
          return (
            <Link to={item.username} className="noLink"><User src={item.pfp} name={item.username}/></Link>
          )
        })}
        </div>
    </div>
  )
}

function Header(){
  const username = localStorage.getItem('username');
  const [open, setOpen] = useState(false);
  const [userEditOpen, setUserEditOpen] = useState(false);

  return (
    <header className="hf-header">
      <Post onClose={() => setOpen(false)} open={open}/>
      <UserEdit onClose={() => setUserEditOpen(false)} open={userEditOpen}/>
      <div className="hf-left">
        <Link to="/"><Button className="hf-left-children" css={{width: "50px", height: "50px", borderColor: "#555"}} bordered auto color="" icon={<HomeIcon fill="#555" size="40px" filled />} /></Link>
      </div>
      <div className="hf-user">
      <Popover placement="bottom-right" bordered css={{backgroundColor: "#000"}}>
          <Popover.Trigger>
          <Avatar className="hf-user-children my-avatar" src={pfpUrl(username)} css={{ size: "50px", marginRight: "10px"}}/>
          </Popover.Trigger>
          <Popover.Content css={{ px: '$4', py: '$2', backgroundColor: "#444" }}>
            <UserPopoverContent></UserPopoverContent>
          </Popover.Content>
        </Popover>
        <Button className="hf-user-children" css={{width: "50px", height: "50px", borderColor: "#555"}} onPress={() => setOpen(true)} bordered auto color="" icon={<PlusIcon fill="#555" size="50px" filled />} />
        <Button className="hf-user-children" css={{width: "50px", height: "50px", borderColor: "#555"}} onPress={() => setUserEditOpen(true)} bordered auto color="" icon={<EditIcon fill="#555" size="40px" filled />} />
        <Popover placement="bottom-right" bordered css={{backgroundColor: "#000"}}>
          <Popover.Trigger>
          <Button className="hf-user-children" css={{width: "50px", height: "50px", borderColor: "#555"}} bordered auto color="" icon={<SearchIcon fill="#555" size="40px" />} />
          </Popover.Trigger>
          <Popover.Content css={{ px: '$4', py: '$2', backgroundColor: "#fff" }}>
            <SearchPopoverContent></SearchPopoverContent>
          </Popover.Content>
        </Popover>
      </div>
    </header>
  );
}

function Footer(){
  return (
    <footer className="hf-footer">
      <h1>Bye</h1>
    </footer>
  )
}

export { Header, Footer };
