import {Textarea, Modal, Text, Image, Button, Loading, Input} from "@nextui-org/react"
import './UserEdit.css'
import {useState} from 'react'
import {pfp, bio, pfpUrl} from '../Api'

export default function UserEdit(props){
    const username = localStorage.getItem('username');
    const oldBio = localStorage.getItem('bio');
    const [image, setImage] = useState(undefined);
    const [loading, setLoading] = useState(false);
    function update(){
        if(image){
            setLoading(true);
            pfp(document.getElementById('files').files[0]).then(response => {
                console.log(response);
                setLoading(false);
                window.location.reload();
            }).catch(error => {
                console.log(error);
                setLoading(false);
            });
        }
        const _bio = document.getElementById('bio').value;
        if(_bio.length > 0 && _bio && _bio !== null && _bio !== undefined && _bio !== oldBio){
            setLoading(true);
            localStorage.setItem('bio', _bio);
            bio(_bio).then(response => {
                console.log(response);
                setLoading(false);
                window.location.reload();
            }).catch(error => {
                console.log(error);
                setLoading(false);
            });
        }
    }
    return(
        <div className="post" onClick={props.onPress}>
            <Modal open={props.open} onClose={() => {props.onClose(); console.log('closed');}} onOpen={() => console.log('opened!')}>
                <Modal.Header>
                    <Text h2>Edit Profile</Text>
                </Modal.Header>
                <Modal.Body>
                    <Image src={!image ? pfpUrl(username) : image}/>
                    <input type="file" id="files" className="hidden" onChange={() => {
                        const file = document.getElementById('files').files[0];
                        if (file) {
                            setImage(URL.createObjectURL(file));
                        }
                    }}/>
                    <label htmlFor="files" className="pfp-input">Change Profile Picture</label>
                    <Input className="hidden"/>
                    <Textarea placeholder="Bio" id="bio" value={oldBio} minRows={1} maxRows={2}/>
                    <Button auto color='primary' disabled={loading} shadow onClick={() => {
                        update();
                    }}>{loading ? <Loading color="currentColor" size="sm" /> : "Post"}</Button>
                </Modal.Body>
            </Modal>
        </div>
    )
}