import {Modal, Text, Input, Image, Button, Loading} from "@nextui-org/react"
import {useState} from 'react'
import {post} from '../Api';
import './Post.css'

export default function Post(props){
    const username = localStorage.getItem('username');
    const [image, setImage] = useState(undefined);
    const [loading, setLoading] = useState(false);
    const [caption, setCaption] = useState("");
    function _post(file){
        setLoading(true);
        post(file, caption).then(response => {
            console.log(response);
            setLoading(false);
            if(window.location.hash === '#/' + username) {
                window.location.reload();
            }
            window.location.href = '#/' + username;
            props.onClose();
        }).catch(error => {
            console.log(error);
            setLoading(false);
        });
    }
    return(
        <div className="post" onClick={props.onPress}>
            <Modal open={props.open} onClose={() => {props.onClose(); console.log('closed');}} onOpen={() => console.log('opened!')}>
                <Modal.Header>
                    <Text h2>Post</Text>
                </Modal.Header>
                <Modal.Body>
                    {image ? (<Image src={image}/>) : null}
                    <Text h4 weight="medium"><span id="username">@{localStorage.getItem('username')}</span> {caption}</Text>
                    <input type="file" id="files" class="hidden" onChange={() => {
                        const file = document.getElementById('files').files[0];
                        if (file) {
                            setImage(URL.createObjectURL(file));
                        }
                    }}/>
                    <label htmlFor="files" className="file-input">Select file</label>
                    <Input placeholder="Caption" id="caption" onChange={(e) => setCaption(e.target.value)}></Input>
                    <Button auto color='primary' disabled={image === undefined || loading} shadow onClick={() => {
                        console.log(image);
                        _post(document.getElementById('files').files[0]);
                    }}>{loading ? <Loading color="currentColor" size="sm" /> : "Post"}</Button>
                </Modal.Body>
            </Modal>
        </div>
    )
}