import {Avatar, Text, Image, Modal, Button} from '@nextui-org/react';
import './PostView.css'
import {HeartIcon} from'../resources/HeartIcon';
import {useState} from 'react';
import {likePost, pfpUrl} from '../Api';
import { Link } from 'react-router-dom';

export default function PostView (props) {
    const [liked, setLiked] = useState(props.likedByYou);
    return (
        <Modal
            closeButton
            aria-labelledby="modal-title"
            open={props.open}
            onClose={() => {props.onClose();}}
            onOpen={() => setLiked(props.likedByYou)}
            blur
        >
            <Modal.Header>
                <Link to={props.username} className="noLink">
                <div className="pressable">
                <Avatar className="pressable" src={pfpUrl(props.username)} css={{ size: "50px", marginRight: "10px"}}/>
                <Text h1>{props.username}</Text>
                </div>
                </Link>
            </Modal.Header>
            <Modal.Body>
                <Image src={props.image} className="postImage"/>
                <div className="likeButton"><Button
                    auto
                    color="error"
                    bordered={liked ? "false" : "true"}
                    onPress={() => {
                        likePost(props.postId);
                        setLiked(!liked);
                        props.toggleLikedByYou();
                    }}
                    shadow={!liked ? "false" : "true"}
                    icon={<HeartIcon fill="currentColor" filled />}
                >{props.likes}</Button></div>
                <Text>{props.caption}</Text>
            </Modal.Body>
            <Modal.Footer>
            </Modal.Footer>
        </Modal>
    )
}